"use client";

import { useState } from "react";

export default function LoginPage() {
  const [username, setUsername] = useState("");
  const [senha, setSenha] = useState("");
  const [error, setError] = useState("");

  const handleLogin = async () => {
    setError("");
    try {
      // Simple login simulation: call backend to verify user
      const res = await fetch("http://localhost:3001/usuarios", {
        headers: {
          "x-user": username,
        },
      });
      if (!res.ok) {
        setError("Usuário não encontrado");
        return;
      }
      const usuarios = await res.json();
      const usuario = usuarios.find((u: any) => u.username === username);
      if (!usuario) {
        setError("Usuário não encontrado");
        return;
      }
      // For demo, no password check here, should be improved
      alert("Login bem-sucedido! Implementar redirecionamento.");
    } catch (err) {
      setError("Erro ao conectar ao servidor");
    }
  };

  return (
    <div className="w-full max-w-md p-6 border border-gray-300 rounded-md shadow-md bg-white">
      <h1 className="text-2xl font-bold mb-6 text-center">Login</h1>
      <label className="block mb-2 font-semibold" htmlFor="username">
        Usuário
      </label>
      <input
        id="username"
        type="text"
        className="w-full p-2 border border-gray-400 rounded mb-4"
        value={username}
        onChange={(e) => setUsername(e.target.value)}
      />
      <label className="block mb-2 font-semibold" htmlFor="senha">
        Senha
      </label>
      <input
        id="senha"
        type="password"
        className="w-full p-2 border border-gray-400 rounded mb-4"
        value={senha}
        onChange={(e) => setSenha(e.target.value)}
      />
      {error && <p className="text-red-600 mb-4">{error}</p>}
      <button
        onClick={handleLogin}
        className="w-full bg-black text-white py-2 rounded hover:bg-gray-800 transition"
      >
        Entrar
      </button>
    </div>
  );
}
